n esfuerzo colectivo que requiere acciones a nivel global y local. Mientras que los acuerdos internacionales pueden establecer normas y objetivos generales, las comunidades locales tienen la responsabilidad de adaptar estas directrices, a sus contextos específicos. De esta manera, el desarrollo sostenible se convierte en una tarea compartida que necesita la colaboración de todos los sectores de la sociedad, incluidos gobiernos, empresas, organizaciones no gubernamentales y ciudadanos. La cooperación entre diferentes niveles de gobierno y comunidades, es vital para alcanzar objetivos globales de sostenibilidad.")])]),_c('div',{staticClass:"tarjeta bg-rojo p-4",attrs:{"x":"20%","y":"35.4%","numero":"+"}},[_c('div',{staticClass:"h5 mb-2"},[_vm._v("Participación y transparencia")]),_c('p',[_vm._v("Es esencial que las decisiones relacionadas con el desarrollo sostenible, se tomen de forma inclusiva y participativa. Esto implica que todos los actores involucrados, desde las comunidades locales hasta las empresas y los gobiernos, deben tener voz en el proceso de toma de decisiones. Además, la transparencia en la gestión de recursos y la implementación de políticas sostenibles, es clave para garantizar la rendición de cuentas y la confianza de la sociedad. La participación activa y el acceso a la información, fortalecen la gobernanza y permiten que las decisiones sean más efectivas y justas.")])]),_c('div',{staticClass:"tarjeta bg-rojo p-4",attrs:{"x":"80%","y":"35.4%","numero":"+"}},[_c('div',{staticClass:"h5 mb-2"},[_vm._v("Equidad intergeneracional")]),_c('p',[_vm._v("Este principio destaca la responsabilidad de la generación actual de no agotar los recursos que futuras generaciones necesitarán. La idea es la de mantener un equilibrio que permita la continuidad de la vida y el bienestar de la humanidad, sin comprometer las posibilidades de desarrollo de quienes vivirán en el futuro. En este sentido, se promueve una utilización prudente de los recursos naturales que permita su renovación.")])]),_c('div',{staticClass:"tarjeta bg-rojo p-4",attrs:{"x":"80%","y":"73.4%","numero":"+"}},[_c('div',{staticClass:"h5 mb-2"},[_vm._v("Enfoque holístico e integrado")]),_c('p',[_vm._v("El desarrollo sostenible no se centra en un único aspecto, sino que aborda, de manera conjunta, problemas económicos, sociales y ambientales. Esto significa que el desarrollo económico debe ir de la mano con la equidad social y la protección del medio ambiente. Un ejemplo claro de este principio se observa cuando las políticas que fomentan la creación de empleo, también consideran el impacto ambiental de las industrias que se van a desarrollar, así como las condiciones laborales de los trabajadores. Esta visión integral asegura que el desarrollo no ocurra a expensas de otros factores cruciales para el bienestar.")])])]),_vm._m(5),_c('p',{staticClass:"mb-4"},[_vm._v(" Los términos sostenibilidad y sustentabilidad son utilizados, frecuentemente, como sinónimos en discursos relacionados con el medio ambiente, el desarrollo económico y la equidad social. Sin embargo, aunque ambos conceptos comparten similitudes y están estrechamente relacionados, tienen matices que los diferencian y que son importantes para entender su aplicación en diferentes contextos. ")]),_c('p',{staticClass:"mb-4"},[_vm._v("A continuación, se profundiza en las definiciones de cada uno, sus orígenes y las diferencias claves que los separan.")]),_c('p',{staticClass:"mb-4"},[_vm._v("Comencemos con la sostenibilidad:")]),_c('div',{staticClass:"row mb-4"},[_c('div',{staticClass:"col-12 col-lg-7",attrs:{"data-aos":"fade-right"}},[_c('AcordionA',{attrs:{"tipo":"b"}},[_c('div',{staticClass:"div",attrs:{"titulo":"¿Qué es?"}},[_c('p',[_vm._v("La sostenibilidad se refiere a la capacidad de mantener o prolongar un proceso, actividad o estado en el tiempo, sin agotar los recursos o causar un daño irreversible al medio ambiente. El concepto surge de la necesidad de asegurar que las actividades humanas, especialmente aquellas relacionadas con el desarrollo económico, no comprometan la capacidad de las generaciones futuras, para satisfacer sus propias necesidades.")])]),_c('div',{staticClass:"div",attrs:{"titulo":"Pilares fundamentales"}},[_c('p',[_vm._v("La sostenibilidad está basada en tres pilares fundamentales: el económico, el social y el ambiental, que deben coexistir y apoyarse mutuamente para lograr un equilibrio a largo plazo.")])])])],1),_vm._m(6)]),_c('div',{staticClass:"row mb-4"},[_vm._m(7),_c('div',{staticClass:"col-12 col-lg-7",attrs:{"data-aos":"fade-left"}},[_c('AcordionA',{attrs:{"tipo":"b"}},[_c('div',{staticClass:"div",attrs:{"titulo":"¿Qué implica?"}},[_c('p',[_vm._v("En la práctica, la sostenibilidad implica diseñar sistemas y procesos que sean regenerativos y resilientes. Por ejemplo, en el contexto empresarial, las prácticas sostenibles pueden incluir la adopción de energías renovables, la gestión eficiente de recursos hídricos, el uso de materiales reciclables y la reducción de emisiones de carbono. Además, la sostenibilidad también aborda aspectos sociales, como la promoción de la igualdad, el bienestar de las comunidades y el respeto por los derechos humanos.")])]),_c('div',{staticClass:"div",attrs:{"titulo":"Ejemplo"}},[_c('p',[_vm._v("Un ejemplo que permite comprender el término sostenibilidad, consiste en una empresa de producción textil que adopta un modelo de sostenibilidad podría transformar todo su ciclo de vida del producto para minimizar el impacto ambiental. Esto incluiría el uso de materiales reciclables y biodegradables, la reducción del uso de agua y energía en la producción, la eliminación de residuos tóxicos, el diseño de productos que sean fáciles de reciclar al final de su vida útil, y la implementación de programas de responsabilidad social que aseguren condiciones laborales justas para todos los trabajadores. Aquí, la sostenibilidad implica la transformación integral de las prácticas, para lograr un equilibrio entre lo económico, lo social y lo ambiental.")])])])],1)]),_c('p',{staticClass:"mb-4"},[_vm._v("Y finalicemos con la sustentabilidad:")]),_c('div',{staticClass:"row mb-4"},[_c('div',{staticClass:"col-12 col-lg-7",attrs:{"data-aos":"fade-right"}},[_c('AcordionA',{attrs:{"tipo":"b"}},[_c('div',{staticClass:"div",attrs:{"titulo":"¿Qué es?"}},[_c('p',[_vm._v("La sustentabilidad, aunque similar a la sostenibilidad, pone un énfasis particular en la capacidad de los sistemas para sostenerse a sí mismos a lo largo del tiempo, utilizando, de manera eficiente y responsable, los recursos disponibles sin agotarlos. La palabra “sustentable” proviene del latín “sustentare”, que significa sostener, soportar o mantener. Este término se asocia frecuentemente, con la gestión de recursos naturales y la capacidad de los ecosistemas para autorregenerarse, asegurando que los recursos que se extraen puedan ser reemplazados o regenerados naturalmente.")])]),_c('div',{staticClass:"div",attrs:{"titulo":"¿En qué se centra?"}},[_c('p',[_vm._v("A diferencia de la sostenibilidad, que aborda un enfoque más amplio e integrado, la sustentabilidad se centra más en la gestión responsable y eficiente de los recursos. En el contexto de la sustentabilidad, se prioriza el uso prudente de los recursos naturales para garantizar que no se agoten a un ritmo más rápido del que la naturaleza puede regenerarlos. Por ejemplo, en el ámbito agrícola, la agricultura sustentable implica prácticas que conservan la fertilidad del suelo, usan agua de manera eficiente y reducen la dependencia de insumos químicos que pueden dañar el medio ambiente.")])])])],1),_vm._m(8)]),_c('div',{staticClass:"row mb-5"},[_vm._m(9),_c('div',{staticClass:"col-12 col-lg-7",attrs:{"data-aos":"fade-left"}},[_c('AcordionA',{attrs:{"tipo":"b"}},[_c('div',{staticClass:"div",attrs:{"titulo":"Aplicación"}},[_c('p',[_vm._v("La sustentabilidad también se aplica a otros aspectos de la vida diaria, como el consumo responsable de energía, agua y alimentos, y la promo